<template>
  <div class="oper-task-manage">
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">数据集成</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">运行任务管理</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>{{item.code}}</td>
                    <td>{{item.name}}</td>
                    <td>
                        <i-switch v-model="item.flag"   @on-change="changeUseSts(item)" >
                            <span slot="open"></span>
                            <span slot="close"></span>
                        </i-switch>
                    </td>
                    <td>
                        <span class="spanBox" @click="subTaskManage()">子任务管理</span>
                    </td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
          <div class="flexCenter pageBox">
            <Page :total="100" show-elevator show-sizer></Page>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      showModal: false,
      dataObj:{
        type: "1", //1新增，2修改，3删除，4历史，5设置
        data:[],
      },
      modelTitle: "新增",
      type: this.$route.params.type,
      tableHeader: [
        { name: "序" },
        { name: "类型代码" },
        { name: "类型名称" },
        { name: "状态" },
        { name: "子任务" },
      ],

      tableBody: [
        {
          id: "12",
          code: "111",
          name: "低",
          roleDescribe: "0",
          Remarks: "50"
        }
      ]
    };
  },
  mounted() {},
  methods:{
    subTaskManage(){
        this.$router.push({name:'subTaskManage'})
    }
  },
  watch: {
  }
};
</script>
<style lang="scss" scoped>
.oper-task-manage {
  .button-content {
    float: right;
  }
}
</style>


